import {
  ADD_ARTICLE,
  UPDATE_ARTICLE,
  DELETE_ARTICLE,
} from "../constants/articlesConstant";

export const addArticle = (data) => {
  return {
    type: ADD_ARTICLE,
    payload: {
      id: data.id,
      title: data.title,
      img: data.img,
      author: data.author,
      article: data.article,
      date: data.date,
    },
  };
};

export const updateArticle = (data) => {
  return {
    type: UPDATE_ARTICLE,
    payload: {
      id: data.id,
      title: data.title,
      article: data.article,
    },
  };
};

export const deleteArticle = (id) => {
  return {
    type: DELETE_ARTICLE,
    payload: id,
  };
};
