import React, { useContext, useState } from "react";
import { UserContext } from "../../context/UserContext";
import { useNavigate } from "react-router-dom";

const SignUpModal = () => {
  const { modalState, toggleModals, signUp } = useContext(UserContext);

  const navigate = useNavigate();

  const [input, setInput] = useState({
    email: "",
    password: "",
    confirmPassword: "",
  });
  const [passwordValidation, setPasswordValidation] = useState("");
  const [emailValidation, setEmailValidation] = useState("");

  const handleForm = async (e) => {
    const { value, name } = e.target;
    setInput({ ...input, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (input.password.length < 6) {
      setPasswordValidation("6 caractères minimum");
      return;
    } else if (input.password !== input.confirmPassword) {
      setPasswordValidation("Les mots de passe doivent êtres identique");
      return;
    } else {
      setPasswordValidation("");
    }

    try {
      await signUp(input.email, input.password);
      closeModals();
      navigate("/formulaire");
    } catch (error) {
      if (error.code === "auth/invalid-email") {
        setEmailValidation("format email invalide");
      }
      if (error.code === "auth/email-already-in-use") {
        setEmailValidation("email déjà utilisé");
      }
    }
  };

  const closeModals = () => {
    toggleModals("close");
    setPasswordValidation("");
    setEmailValidation("");
    setInput({ email: "", password: "", confirmPassword: "" });
  };

  return (
    <>
      {modalState.signUpModal && (
        <div className="registration">
          <div className="registration__overlay" onClick={closeModals}></div>

          <form className="registration__form" onSubmit={handleSubmit}>
            <button className="registration__close" onClick={closeModals}>
              X
            </button>
            <label htmlFor="email">Adresse e-mail</label>
            <input
              type="email"
              id="email"
              name="email"
              value={input.email}
              onChange={handleForm}
            />

            <div className="registration__alert">{emailValidation}</div>

            <label htmlFor="password">Mot de passe</label>
            <input
              type="password"
              id="password"
              name="password"
              value={input.password}
              onChange={handleForm}
            />

            <label htmlFor="confirmPassword">Comfirmer mot de passe</label>
            <input
              type="password"
              id="confirmPassword"
              name="confirmPassword"
              value={input.confirmPassword}
              onChange={handleForm}
            />

            <div className="registration__alert">{passwordValidation}</div>

            <div className="registration__switch">
              Déjà inscrit ?{" "}
              <span onClick={() => toggleModals("signIn")}>Se connecter</span>
            </div>

            <button className="registration__btn" type="submit">
              Inscription
            </button>
          </form>
        </div>
      )}
    </>
  );
};

export default SignUpModal;
