import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { addArticle } from "../../redux/actions/articlesAction";
import { v4 as uuidv4 } from "uuid";
import { useContext } from "react";
import { UserContext } from "../../context/UserContext";
import { Navigate } from "react-router-dom";

const FormScreen = () => {
  const dispatch = useDispatch();
  const { user } = useContext(UserContext);

  const date = new Date().toLocaleString().split(",")[0];

  const [inputValue, setInputValue] = useState({
    id: uuidv4(),
    title: "",
    img: "",
    author: user,
    article: "",
    date,
  });

  const [alertTitle, setAlertTitle] = useState("");
  const [AlertArticle, setAlertArticle] = useState("");

  const handleChange = (e) => {
    const { value, name } = e.target;
    setInputValue({ ...inputValue, [name]: value });
    setAlertTitle("");
    setAlertArticle("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(addArticle(inputValue));

    if (inputValue.title.length <= 0) {
      return setAlertTitle("Veuillez entrer un titre");
    }
    if (inputValue.article.length <= 0) {
      return setAlertArticle("Veuillez entrer un texte");
    }

    setInputValue({
      id: uuidv4(),
      title: "",
      img: "",
      author: user,
      article: "",
      date,
    });
  };

  if (!user) {
    return <Navigate to="/" />;
  }

  return (
    <form className="container form" onSubmit={handleSubmit}>
      <label htmlFor="title" className="form__label">
        Titre
      </label>
      <input
        type="text"
        className="form__input"
        id="title"
        name="title"
        value={inputValue.title}
        onChange={handleChange}
        // required
      />
      {alertTitle}

      <label htmlFor="img" className="form__label">
        URL image
      </label>
      <input
        type="text"
        className="form__input"
        id="img"
        name="img"
        value={inputValue.img}
        onChange={handleChange}
      />

      <label htmlFor="article" className="form__label">
        Article
      </label>
      <textarea
        className="form__input form__input--textarea"
        id="article"
        name="article"
        value={inputValue.article}
        onChange={handleChange}
        // required
      />

      {AlertArticle}

      <button className="form__btn" type="submit">
        Publier
      </button>
    </form>
  );
};

export default FormScreen;
