import React, { useContext } from "react";
import { UserContext } from "../../context/UserContext";
import { NavLink } from "react-router-dom";
import { signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { auth } from "../../firebase-config";

const Header = () => {
  const navigate = useNavigate();
  const { toggleModals, user } = useContext(UserContext);

  const logOut = async () => {
    try {
      await signOut(auth);
      navigate("/");
    } catch (error) {
      alert("Erreur lors de la déconnexion, veuillez réessayer.");
    }
  };

  return (
    <nav className="container navigation">
      {user && (
        <ul className="navigation__list">
          <li className="navigation__item">
            <NavLink to="/" activeclassname="active">
              Articles
            </NavLink>
          </li>
          <li className="navigation__item">
            <NavLink to="/formulaire" activeclassname="active">
              Formulaire
            </NavLink>
          </li>
        </ul>
      )}

      <div className="navigation__identification">
        {!user && (
          <>
            <div>Se connecter pour écrire un article</div>
            <button
              className="navigation__btn navigation__btn--black"
              onClick={() => toggleModals("signIn")}
            >
              Connexion
            </button>

            <button
              className="navigation__btn navigation__btn--black"
              onClick={() => toggleModals("signUp")}
            >
              Inscription
            </button>
          </>
        )}

        {user && (
          <>
            <div className="">Bienvenue {user} !</div>
            <button
              className="navigation__btn navigation__btn--red"
              onClick={logOut}
            >
              Déconnexion
            </button>
          </>
        )}
      </div>
    </nav>
  );
};

export default Header;
