import React, { useState } from "react";
import { useLocation } from "react-router";
import Buttons from "../../components/Buttons/Buttons";
import { NavLink } from "react-router-dom";

const Details = () => {
  const location = useLocation();
  const article = location.state;

  const [toggleUpdate, setToggleUpdate] = useState(false);
  const [inputState, setInputState] = useState(article);

  return (
    <main className="container details">
      <NavLink to="/" activeclassname="active">
        Retour
      </NavLink>
      {inputState.img && (
        <img src={article.img} alt={article.title} className="details__img" />
      )}

      {!toggleUpdate ? (
        <>
          <h3 className="details__title">{inputState.title}</h3>
          <p className="details__text">{inputState.article}</p>
        </>
      ) : (
        <>
          <input
            type="text"
            className="details__title"
            name="title"
            value={inputState.title}
            onChange={(e) =>
              setInputState({ ...inputState, title: e.target.value })
            }
          ></input>
          <textarea
            className="details__text"
            name="article"
            value={inputState.article}
            onChange={(e) =>
              setInputState({ ...inputState, article: e.target.value })
            }
          ></textarea>
        </>
      )}

      <div className="details__footer">
        <div className="details__infos">
          <span className="details__author">écrit par {article.author}</span>
          <span className="details__time"> le {article.date} </span>
        </div>
        <div className="details__btns">
          <Buttons
            toggleUpdate={toggleUpdate}
            setToggleUpdate={setToggleUpdate}
            inputState={inputState}
            setInputState={setInputState}
            article={article}
          />
        </div>
      </div>
    </main>
  );
};

export default Details;
