import React, { useContext, useState } from "react";
import { UserContext } from "../../context/UserContext";
import { useNavigate } from "react-router-dom";

const SignInModal = () => {
  const { modalState, toggleModals, signIn } = useContext(UserContext);

  const navigate = useNavigate();

  const [input, setInput] = useState({
    email: "",
    password: "",
  });

  const [validation, setValidation] = useState("");

  const handleForm = async (e) => {
    const { value, name } = e.target;
    setInput({ ...input, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await signIn(input.email, input.password);

      closeModals();
      setValidation("");
      navigate("/formulaire");
    } catch {
      setValidation("Email ou mot de passe incorrect");
    }
  };

  const closeModals = () => {
    toggleModals("close");
    setValidation("");

    setInput({ email: "", password: "" });
  };

  return (
    <>
      {modalState.signInModal && (
        <div className="registration">
          <div className="registration__overlay" onClick={closeModals}></div>

          <form className="registration__form" onSubmit={handleSubmit}>
            <button className="registration__close" onClick={closeModals}>
              X
            </button>
            <label htmlFor="email">Adresse e-mail</label>
            <input
              type="email"
              id="email"
              name="email"
              value={input.email}
              onChange={handleForm}
            />

            <label htmlFor="password">Mot de passe</label>
            <input
              type="password"
              id="password"
              name="password"
              value={input.password}
              onChange={handleForm}
            />

            <div className="registration__alert">{validation}</div>

            <div className="registration__switch">
              Pas encore inscrit ?{" "}
              <span onClick={() => toggleModals("signUp")}>S'inscrire</span>
            </div>

            <button className="registration__btn" type="submit">
              Connexion
            </button>
          </form>
        </div>
      )}
    </>
  );
};

export default SignInModal;
