import React from "react";
import { useDispatch } from "react-redux";
import {
  updateArticle,
  deleteArticle,
} from "../../redux/actions/articlesAction";
import { useNavigate } from "react-router-dom";

const Update = ({
  toggleUpdate,
  setToggleUpdate,
  inputState,
  setInputState,
  article,
}) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleSubmit = () => {
    setToggleUpdate(false);

    if (
      inputState.title !== article.title ||
      inputState.article !== article.article
    ) {
      dispatch(updateArticle(inputState));
      navigate("/");
    }
  };

  const handleDelete = () => {
    dispatch(deleteArticle(article.id));
    navigate("/");
  };

  const handleCancel = () => {
    setInputState(article);
    setToggleUpdate(false);
  };
  return (
    <div className="btns">
      {!toggleUpdate ? (
        <button className="btns--update" onClick={() => setToggleUpdate(true)}>
          Modifier
        </button>
      ) : (
        <>
          <button className="btns--cancel" onClick={handleCancel}>
            Annuler
          </button>
          <button className="btns--send" onClick={handleSubmit}>
            Valider
          </button>
        </>
      )}

      <button className="btns--delete" onClick={handleDelete}>
        Supprimer
      </button>
    </div>
  );
};

export default Update;
