export const articlesReducer = (state = INITIAL_STATE, action) => {
  if (localStorage.getItem("articles")) {
    state = JSON.parse(localStorage.getItem("articles"));
  } else {
    localStorage.setItem("articles", JSON.stringify(state));
  }

  switch (action.type) {
    case ADD_ARTICLE:
      state.articles = [...state.articles, action.payload];
      localStorage.setItem("articles", JSON.stringify(state));
      return state;

    case UPDATE_ARTICLE:
      state.articles = state.articles.map((item) => {
        if (item.id === action.payload.id) {
          return {
            ...item,
            title: action.payload.title,
            article: action.payload.article,
          };
        } else {
          return {
            ...item,
          };
        }
      });

      localStorage.setItem("articles", JSON.stringify(state));

      return state;

    case DELETE_ARTICLE:
      state.articles = state.articles.filter((item) => {
        return item.id !== action.payload;
      });

      localStorage.setItem("articles", JSON.stringify(state));

      return state;

    default:
      return state;
  }
};
