import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const ArticlesScreen = () => {
  const data = useSelector((state) => ({
    ...state.articlesReducer,
  }));

  return (
    <main className="container articles">
      {data.articles.map((item) => {
        return (
          <Link key={item.id} to={`/${item.title}`} state={item}>
            <article className="articles__card">
              {item.img && (
                <img
                  className="articles__img"
                  src={item.img}
                  alt="illustration"
                />
              )}
              <div className="articles__content">
                <h3 className="articles__title">{item.title}</h3>
                <p className="articles__text">{item.article}</p>
                <span className="articles__author">{item.author}</span>
                <span className="articles__time">{item.date}</span>
              </div>
            </article>
          </Link>
        );
      })}
    </main>
  );
};

export default ArticlesScreen;
